.success {
  color: rgb(10, 68, 10);
  background: rgb(89, 202, 74);
  font-size: 20px;
  border-style: none;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}